
















import { Input, Button } from "iview";
import { Component, Vue } from "vue-property-decorator";
import { Message, Notice } from "iview";
import utils from "../utils";

declare let chrome: any;

// Todo delete 测试代码
if (!chrome || !chrome.storage) {
  chrome.storage = {
    sync: {
      get: () => "",
      set: (_: any, callback: Function) => callback()
    }
  };
}

@Component({
  components: { "i-input": Input, "i-button": Button }
})
export default class extends Vue {
  groupId: string = "";
  handle: boolean = false;
  created() {
    chrome.storage.sync.get({ groupId: "" }, (res: { groupId: string }) => {
      this.groupId = res.groupId;
    });
  }
  protected get $message(): any {
    return Message;
  }
  protected get $notice(): any {
    return Notice;
  }
  question(): void {
    this.$notice.open({
      title: "获取GroupId",
      render: (h: any) => {
        return h("span", [
          "邮件至",
          h(
            "a",
            { attrs: { href: "mailto:cjwddz@gmail.com" } },
            "cjwddz@gmail.com"
          ),
          "或",
          h(
            "a",
            { attrs: { href: "mailto:lilejia@bigo.sg" } },
            "lilejia@bigo.sg"
          ),
          "获取GroupId"
        ]);
      },
      duration: 15000
    });
  }
  // methods
  confirm(): void {
    if (!this.groupId) {
      this.$message.error("请输入先groupId");
      return;
    }
    this.handle = true;
    const msg = this.$message.loading({
      content: "同步配置中...",
      duration: 0
    });
    utils.RMI("updateConfig", this.groupId).then((res: any) => {
      msg();
      this.handle = false;
      if (res.status !== 0) {
        this.$message.error(res.result || "请输入有效的GroupId");
        this.$emit("updateConfig", false);
        return;
      }
      this.$message.success("设置成功！");
      this.$emit("updateConfig", true);
    });
  }
}
